<template>
  <div class="mapTable" >
    <div class="btnBox" v-show="!spotTable" @click="spotTable=true">
      <i-button icon="ios-chatboxes"></i-button>
    </div>
    <div class="modelBox" v-if="spotTable">
      <p>
        <strong class="active"  @click="switchTab(0)">
          问题点位
        </strong>
        <!--<Button type="primary" size="small" style="float: right;margin:5px 8px" @click="exportPointExcel">导出</Button>-->
      </p>
      <div class="table_box">
        <Table stripe :columns="columns1" :data="data1" size="small" v-show="telescopic" :height="tableHeight" highlight-row></Table>
        <Page :total="total" :page-size="pageSize" size="small" class="thePage" @on-change="pageChange" :current="pageNum" show-total></Page>
      </div>
    </div>
  </div>
</template>

<script>
import { getQuestionList } from '@/api/mobilesSupervision'
import { cacuLonLat, cacuDMS, renderTime } from '@/utils/common'

export default {
  name: 'MapTable',
  data () {
    return {
      /* 表格高度 */
      tableHeight: window.innerHeight - 327,
      /* 列表框打开 */
      spotTable: true,
      /* 列表伸缩框打开 */
      telescopic: true,
      /* 登记表查询id */
      tabletId: '',
      /* 原始界桩表头 */
      columns1: [
        {
          title: '编号',
          key: 'tqvSerivalNumber',
          width: 180,
          fixed: 'left',
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('a', {
                style: {
                  color: '#2d8cf0',
                  textDecoration: 'underline'
                },
                on: {
                  click: () => {
                    this.ondbclickTd(cacuDMS(params.row.tqvLng), cacuDMS(params.row.tqvLat), params.row.tqvId)
                  }
                }
              }, params.row.tqvSerivalNumber)
            ])
          }
        },
        {
          title: '活动/设施名称',
          key: 'tqvFacilityName',
          align: 'center',
          minWidth: 100
        },
        {
          title: '活动/设施类型',
          key: 'tqvFacilityType',
          align: 'center',
          minWidth: 100
        },
        {
          title: '所在功能区',
          key: 'tqvSectorization',
          align: 'center',
          minWidth: 100
        },
        {
          title: '填表人',
          key: 'tqvPreparer',
          align: 'center',
          minWidth: 100
        },
        {
          title: '核查时间',
          key: 'tqvCheckTime',
          align: 'center',
          minWidth: 100,
          render: (h, params) => {
            return h('span', renderTime(params.row.tqvCheckTime))
          }
        },
        {
          title: '经度',
          key: 'lpLon',
          align: 'center',
          minWidth: 100,
          render: (h, params) => {
            return h('span', cacuLonLat(cacuDMS(params.row.tqvLng)))
          }
        },
        {
          title: '纬度',
          key: 'lpLat',
          align: 'center',
          minWidth: 100,
          render: (h, params) => {
            return h('span', cacuLonLat(cacuDMS(params.row.tqvLat)))
          }
        }
      ],
      /* 表格数据 */
      data1: [],
      /* 页码 */
      pageNum: 1,
      /* 每页条数 */
      pageSize: Math.floor((window.innerHeight - 305) / 40) - 1,
      /* 总条数 */
      total: 0
    }
  },
  props: {
    obj: {},
    srld: null
  },
  components: {},
  watch: {
    obj () {
      this.getTableData()
    },
    pageNum () {
      this.getTableData()
    },
    spotTable (val) {
      this.$emit('mapSizeChange', val)
    },
    srld (val) {
      this.pageNum = 1
      this.getTableData()
    }
  },
  created () {
    this.getTableData()
  },
  mounted () {
    // 窗口改变时图表自适应
    window.addEventListener('resize', () => {
      this.tableHeight = window.innerHeight - 327
      this.pageSize = Math.floor((window.innerHeight - 305) / 40) - 1
      this.getTableData()
    }, false)
  },
  methods: {
    /* 获取拐点坐标列表 */
    getTableData () {
      getQuestionList({
        data: {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          tqv_serival_number: this.obj.code,
          addUser: this.obj.filler,
          startDate: this.obj.vertime === undefined ? '' : this.obj.vertime[0],
          endDate: this.obj.vertime === undefined ? '' : this.obj.vertime[1]
        }
      }).then((res) => {
        this.data1 = res.data.data.rows
        this.total = res.data.data.total
        this.$emit('getPointData', res.data.data.rows)
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误'})
      })
    },
    /* 双击控制地图跳转经纬度 */
    ondbclickTd (lon, lat, id) {
      this.$emit('ondbclickDingwei', lon, lat, id)
    },
    /* 分页跳转 */
    pageChange (e) {
      this.pageNum = e
    },
    /* 日期格式转换 */
    renderTime (date) {
      let d = new Date(date)
      let times = d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate()
      return times
    }
  }
}
</script>

<style lang="scss" scoped rel="stylesheet/scss">
  .mapTable{
    height:100%
  }
  .btnBox{
    position: absolute;right:10px;top:10px;
  }
  .modelBox{
    background: #F5F5F5;
    height:100%;
    margin-right: -1px;
    p{
      padding:0 15px 0 0;
      position: relative;
      overflow: hidden;
      text-align: left;
      border: 1px #d1d1d1 solid;
      &:before{
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height:0;
      }
      strong{
        float: left;
        display: inline-block;
        cursor: pointer;
        height: 100%;
        font-size:14px;
        padding:5px 20px;
      }
      .active{
        border: 1px solid #DADCE0;
        background: #fff;
        border-bottom: 0;
      }
      span{
        float: right;
        cursor: pointer;
      }
    }
    .table_box{
      padding:0 0 45px;
      .thePage{
        position: absolute;
        bottom:10px;
        right:10px;
      }
    }
  }
</style>
