<template>
  <div class="textTerm">
    <span style="width: 100px;display: inline-block">{{titName}}：</span>
    <DatePicker size="small" type="daterange"
                v-model="UnitName"
                @on-change="srldChange()"
                :start-date="new Date()" placement="bottom-end"
                placeholder="请输入查询起止日期" style="width: 200px"></DatePicker>
  </div>
</template>

<script>
import { renderTime } from '@/utils/common'
export default {
  name: 'TextTerm',
  data () {
    return {
      /* input内容 */
      UnitName: ''
    }
  },
  props: {
    /* 字段标题 */
    titName: {},
    /* 字段key值 */
    titKey: {}
  },
  mounted () {

  },
  methods: {
    srldChange () {
      if (this.UnitName === null || (this.UnitName[0] === '' && this.UnitName[1] === '')) {
        this.$emit('delQueryTrem', this.titKey)
      } else {
        let obj = {
          name: this.titName,
          text: renderTime(this.UnitName[0]) + ' - ' + renderTime(this.UnitName[1]),
          key: this.titKey,
          value: this.UnitName
        }
        this.$emit('updataQueryData', obj)
      }
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  .cascadeTerm{

  }
</style>
